.container {
  height: 65vh;
  width: 100vw;
}

.card {
  display: flex;
  justify-content: center;
}

.variableHeader {
  height: 5vh;

  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  font-size: x-large;
  font-weight: bold;
}

.variableBody {
  height: 5vh;

  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
}

.button {
  display: flex;
  align-items: center;
  justify-content: space-around;
  font-size: x-small;
}
